// Global variables
let currentSlide = 0;
let slides = [];
let totalSlides = 0;
let carouselTrack = null;

// DOM Content Loaded
document.addEventListener('DOMContentLoaded', function() {
    initializeNavigation();
    initializeCarousel();
    initializeModal();
    initializeScrollEffects();
    initializeSocialLinks();
    initializeFacilityCards();
});

// Social Media Links
function initializeSocialLinks() {
    const socialLinks = document.querySelectorAll('.social-link');
    socialLinks.forEach(link => {
        link.addEventListener('click', function(e) {
            console.log('Social link clicked:', this.href);
            // Don't prevent default - let the link work normally
        });
    });
}

// Facility Cards
function initializeFacilityCards() {
    const facilityCards = document.querySelectorAll('.service-card[data-modal]');
    facilityCards.forEach(card => {
        card.addEventListener('click', function() {
            const modalId = this.getAttribute('data-modal');
            openModal(modalId);
        });
    });
}

// Navigation Functions
function initializeNavigation() {
    const navbar = document.getElementById('navbar');
    const navLinks = document.querySelectorAll('.nav-link');
    
    // Smooth scrolling for navigation links (only internal links)
    navLinks.forEach(link => {
        link.addEventListener('click', function(e) {
            const targetId = this.getAttribute('href');
            
            // Only prevent default and smooth scroll for internal links (starting with #)
            if (targetId && targetId.startsWith('#')) {
                e.preventDefault();
                const targetSection = document.querySelector(targetId);
                
                if (targetSection) {
                    const offsetTop = targetSection.offsetTop - 80; // Account for fixed navbar
                    window.scrollTo({
                        top: offsetTop,
                        behavior: 'smooth'
                    });
                }
            }
            // For external links (like social media), let them work normally
        });
    });

    // Navbar scroll effects
    window.addEventListener('scroll', function() {
        const scrollTop = window.pageYOffset;
        
        // Add scrolled class for navbar resizing
        if (scrollTop > 100) {
            navbar.classList.add('scrolled');
        } else {
            navbar.classList.remove('scrolled');
        }
        
        // Update active navigation link
        updateActiveNavLink();
    });
}

// Position Indicator - Update active navigation link based on scroll position
function updateActiveNavLink() {
    const sections = document.querySelectorAll('section[id]');
    const navLinks = document.querySelectorAll('.nav-link');
    const navbarHeight = document.getElementById('navbar').offsetHeight;
    
    let currentSection = '';
    
    sections.forEach(section => {
        const sectionTop = section.offsetTop - navbarHeight - 50;
        const sectionHeight = section.offsetHeight;
        const scrollTop = window.pageYOffset;
        
        if (scrollTop >= sectionTop && scrollTop < sectionTop + sectionHeight) {
            currentSection = section.getAttribute('id');
        }
    });
    
    // Special case for bottom of page
    const documentHeight = document.documentElement.scrollHeight;
    const windowHeight = window.innerHeight;
    const scrollTop = window.pageYOffset;
    
    if (scrollTop + windowHeight >= documentHeight - 100) {
        currentSection = 'contact'; // Last section
    }
    
    // Update active link
    navLinks.forEach(link => {
        link.classList.remove('active');
        if (link.getAttribute('href') === `#${currentSection}`) {
            link.classList.add('active');
        }
    });
}

// Carousel Functions
function initializeCarousel() {
    slides = document.querySelectorAll('.carousel-slide');
    totalSlides = slides.length;
    carouselTrack = document.getElementById('carousel-track');
    
    if (slides.length === 0) return;
    
    // Initialize carousel
    updateCarousel();
    updateDots();
    
    // Add event listeners for carousel buttons
    addCarouselEventListeners();
    
    // Auto-advance carousel every 6 seconds
    setInterval(() => {
        changeSlide(1);
    }, 6000);
    
    // Add touch/swipe support
    addTouchSupport();
}

function addCarouselEventListeners() {
    // Carousel navigation buttons
    const prevBtn = document.querySelector('.carousel-btn.prev');
    const nextBtn = document.querySelector('.carousel-btn.next');
    
    if (prevBtn) {
        prevBtn.addEventListener('click', () => changeSlide(-1));
    }
    
    if (nextBtn) {
        nextBtn.addEventListener('click', () => changeSlide(1));
    }
    
    // Carousel dots
    const dots = document.querySelectorAll('.dot');
    dots.forEach((dot, index) => {
        dot.addEventListener('click', () => goToSlide(index));
    });
}

function changeSlide(direction) {
    const previousSlide = currentSlide;
    currentSlide += direction;
    
    // Circular motion logic
    if (currentSlide >= totalSlides) {
        currentSlide = 0;
    } else if (currentSlide < 0) {
        currentSlide = totalSlides - 1;
    }
    
    console.log(`Carousel: ${previousSlide} → ${currentSlide} (direction: ${direction})`);
    
    updateCarousel();
    updateDots();
}

function goToSlide(index) {
    currentSlide = index;
    updateCarousel();
    updateDots();
}

function updateCarousel() {
    if (!carouselTrack) return;
    
    // Move the track horizontally
    const translateX = -currentSlide * 100;
    carouselTrack.style.transform = `translateX(${translateX}%)`;
    
    // Update slide classes for opacity effects with proper circular logic
    slides.forEach((slide, index) => {
        slide.classList.remove('active', 'prev-slide', 'next-slide');
        
        if (index === currentSlide) {
            slide.classList.add('active');
        } else {
            // Calculate previous and next slide indices with circular logic
            const prevIndex = currentSlide === 0 ? totalSlides - 1 : currentSlide - 1;
            const nextIndex = currentSlide === totalSlides - 1 ? 0 : currentSlide + 1;
            
            if (index === prevIndex) {
                slide.classList.add('prev-slide');
            } else if (index === nextIndex) {
                slide.classList.add('next-slide');
            }
        }
    });
}

function updateDots() {
    const dots = document.querySelectorAll('.dot');
    dots.forEach((dot, index) => {
        dot.classList.remove('active');
        if (index === currentSlide) {
            dot.classList.add('active');
        }
    });
}

function addTouchSupport() {
    let startX = 0;
    let endX = 0;
    let isDragging = false;
    
    carouselTrack.addEventListener('touchstart', (e) => {
        startX = e.touches[0].clientX;
        isDragging = true;
    });
    
    carouselTrack.addEventListener('touchmove', (e) => {
        if (isDragging) {
            e.preventDefault(); // Prevent scrolling
        }
    });
    
    carouselTrack.addEventListener('touchend', (e) => {
        if (isDragging) {
            endX = e.changedTouches[0].clientX;
            handleSwipe();
            isDragging = false;
        }
    });
    
    // Also add mouse support for desktop
    carouselTrack.addEventListener('mousedown', (e) => {
        startX = e.clientX;
        isDragging = true;
        e.preventDefault();
    });
    
    carouselTrack.addEventListener('mousemove', (e) => {
        if (isDragging) {
            e.preventDefault();
        }
    });
    
    carouselTrack.addEventListener('mouseup', (e) => {
        if (isDragging) {
            endX = e.clientX;
            handleSwipe();
            isDragging = false;
        }
    });
    
    carouselTrack.addEventListener('mouseleave', () => {
        isDragging = false;
    });
    
    function handleSwipe() {
        const swipeThreshold = 50;
        const diff = startX - endX;
        
        if (Math.abs(diff) > swipeThreshold) {
            if (diff > 0) {
                // Swipe left - next slide
                changeSlide(1);
            } else {
                // Swipe right - previous slide
                changeSlide(-1);
            }
        }
    }
}

// Modal Functions
function initializeModal() {
    // Add event listener for close button
    const closeBtn = document.querySelector('.close');
    if (closeBtn) {
        closeBtn.addEventListener('click', function() {
            const modalId = this.getAttribute('data-modal');
            closeModal(modalId);
        });
    }
    
    // Close modal when clicking outside
    window.addEventListener('click', function(e) {
        if (e.target.classList.contains('modal')) {
            e.target.classList.remove('show');
        }
    });
    
    // Close modal with Escape key
    document.addEventListener('keydown', function(e) {
        if (e.key === 'Escape') {
            const openModal = document.querySelector('.modal.show');
            if (openModal) {
                openModal.classList.remove('show');
            }
        }
    });
}

function openModal(modalId) {
    const modal = document.getElementById(modalId);
    if (modal) {
        modal.classList.add('show');
        document.body.style.overflow = 'hidden'; // Prevent background scrolling
    }
}

function closeModal(modalId) {
    const modal = document.getElementById(modalId);
    if (modal) {
        modal.classList.remove('show');
        document.body.style.overflow = 'auto'; // Restore scrolling
    }
}

// Scroll Effects and Animations
function initializeScrollEffects() {
    // Intersection Observer for fade-in animations
    const observerOptions = {
        threshold: 0.1,
        rootMargin: '0px 0px -50px 0px'
    };
    
    const observer = new IntersectionObserver(function(entries) {
        entries.forEach(entry => {
            if (entry.isIntersecting) {
                entry.target.style.opacity = '1';
                entry.target.style.transform = 'translateY(0)';
            }
        });
    }, observerOptions);
    
    // Observe elements for animation
    const animatedElements = document.querySelectorAll('.service-card, .about-content, .contact-content');
    animatedElements.forEach(el => {
        el.style.opacity = '0';
        el.style.transform = 'translateY(30px)';
        el.style.transition = 'opacity 0.6s ease, transform 0.6s ease';
        observer.observe(el);
    });
}

// Utility Functions
function debounce(func, wait) {
    let timeout;
    return function executedFunction(...args) {
        const later = () => {
            clearTimeout(timeout);
            func(...args);
        };
        clearTimeout(timeout);
        timeout = setTimeout(later, wait);
    };
}

// Performance optimization for scroll events
const debouncedScrollHandler = debounce(function() {
    updateActiveNavLink();
}, 10);

window.addEventListener('scroll', debouncedScrollHandler);

// Functions are now handled through event listeners - no global assignment needed

